<template>
  <div class="gamePlayers">
    <div
      v-for="(player) in game.players"
      class="gamePlayers_player"
      :key="player.id"
    >
      <div
        class="gamePlayers_player_color"
        :class="[{'gamePlayers_player_color-active' : player.id === game.player.id}, 'gamePlayers_player_color-' + player.id]"
      >{{player.size}}</div>
      <div class="gamePlayers_player_reserve">(+{{player.reserve}})</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'GamePlayers',
  computed: mapState({
    game: state => state.game
  }),
  data: () => ({
    newGameDialog: false
  }),
  methods: {}
}
</script>

<style scoped lang="scss">
.gamePlayers {
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  width: 100vw;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  scroll-snap-type: x mandatory;
  &_player {
    display: flex;
    align-items: center;
    margin: 0 10px;
    position: relative;
    scroll-snap-align: center;
    flex-direction: column;
    justify-content: center;
    &_color {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      position: relative;
      font-size: 32px;
      line-height: 34px;
      text-align: center;
      box-sizing: border-box;
      border-width: 3px;
      border-style: solid;
      color: rgba(0,0,0,0.87);
      transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      &-active {
        color: #fff;
      }
    }
    &_reserve {
      margin-top: 6px;
    }
  }
  @each $i, $color in $colors {
    &_player {
      &_color {
        &-#{$i} {
          border-color: $color;
          &.gamePlayers_player_color-active{
            background-color: $color;
          }
        }
      }
    }
  }
}
</style>
